/* Footer.css */
.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #333;
    color: white;
    margin-top: 50px;
    background-color: rgba(0, 0, 0, 0);  
}
  
  .social-icons {
    display: flex;
    gap: 10px;
  }
  
  .social-icon {
    font-size: 24px;
    color: white;
    text-decoration: none;
  }
  
  .social-icon:hover {
    color: #3c5775;
  }
  